<template>
  <div id="app">
    <game ref="game">
    </game>
    <cmd-line :input="input" @sendcmd="passCmd">
    </cmd-line>
  </div>
</template>

<script>
import Game from './components/Game'
import CmdLine from './components/CmdLine'

export default {
  name: 'App',
  components: {
    Game, CmdLine
  },
  data: function () {
    return {
      showStartPage: true,
      input: null,
      command: null
    }
  },
  methods: {
    passCmd: function (cmd) {
      this.$refs.game.execute(cmd)
    }
  }
}
</script>

<style>
html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
</style>



